<template>
    <a-form ref="formRef"
            name="password-form"
            :colon="false"
            :rules="rules"
            :model="formData"
            :label-col="{ span: 4 }"
            :wrapper-col="{ span: 20 }"
            autocomplete="off"
            @finish="handleFinish"
            >
        <a-form-item has-feedback label="" name="password">
            <a-input v-model:value="formData.password" type="password" size="large" placeholder="" />
        </a-form-item>
        <a-form-item has-feedback label="" name="newPassword">
            <a-input v-model:value="formData.newPassword" type="password" size="large" placeholder=""/>
        </a-form-item>
        <a-form-item has-feedback label="" name="confirmPassword">
            <a-input v-model:value="formData.confirmPassword" type="password" size="large" placeholder=""/>
        </a-form-item>
        <a-form-item :wrapper-col="{ span: 20, offset: 4 }" :style="{textAlign: 'right'}">
            <a-space>
                <a-button @click="handleCancel"></a-button>
                <a-button type="primary" html-type="submit"></a-button>
            </a-space>
        </a-form-item>
    </a-form>
</template>
<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import {
    LockOutlined
} from '@ant-design/icons-vue'
import { Rule } from 'ant-design-vue/es/form/interface'
import { ref } from 'vue'
import request from '@/request/PlatformRequest'
import { message } from 'ant-design-vue'

@Options({
    components: {
        LockOutlined
    },
    emits: ['close']
})
export default class Password extends Vue {
    // 
    formRef = ref<AntType.Form>()

    // 
    formData = {
        password: '',
        newPassword: '',
        confirmPassword: ''
    }

    validateNewPassword = async (_rule: Rule, value: string) => {
        if (value === '') {
            return Promise.reject(new Error(''))
        } else {
            if (this.formData.newPassword.length < 6) {
                return Promise.reject(new Error('6'))
            }
            if (this.formData.password === value) {
                return Promise.reject(new Error(''))
            }
            if (this.formData.confirmPassword !== '') {
                this.formRef.value?.validate('confirmPassword')
            }
            return Promise.resolve()
        }
    }

    validateConfirmPassword = async (_rule: Rule, value: string) => {
        if (value === '') {
            return Promise.reject(new Error(''))
        } else if (value !== this.formData.newPassword) {
            return Promise.reject(new Error(''))
        } else {
            return Promise.resolve()
        }
    }

    // 
    rules: Record<string, Rule[]> = {
        password: [{ required: true, message: '' }],
        newPassword: [{ required: true, min: 6, validator: this.validateNewPassword, validateTrigger: 'change' }],
        confirmPassword: [{ required: true, validator: this.validateConfirmPassword, validateTrigger: 'change' }]
    }

    // 
    handleCancel() {
        this.formData = {
            password: '',
            newPassword: '',
            confirmPassword: ''
        }
        this.formRef.value?.resetFields()
        this.formRef.value?.clearValidate()
        this.$emit('close')
    }

    // 
    handleFinish(values: unknown) {
        const data = {
            password: this.formData.password,
            newPassword: this.formData.newPassword
        }
        request.changePassword(data).then(res => {
            if (res.code === 0) {
                message.success('')
                this.handleCancel()
            } else {
                message.error(': ' + res.msg)
            }
        })
    }
}
</script>
