
import { Options, Vue } from 'vue-class-component'
import {
    UserOutlined,
    MailOutlined,
    MobileOutlined,
    CrownOutlined,
    PlusOutlined,
    LoadingOutlined
} from '@ant-design/icons-vue'
import { Rule } from 'ant-design-vue/es/form/interface'
import { ref, toRaw } from 'vue'
import request from '@/request/PlatformRequest'
import { message } from 'ant-design-vue'
import { User } from '@/types/User'
import { R } from '@/types/R'
import PubUtils from '@/utils/PubUtils'

interface FileItem {
  uid: string;
  name?: string;
  status?: string;
  response?: R<any>;
  url?: string;
  type?: string;
  size: number;
  originFileObj: any;
}

interface FileInfo {
  file: FileItem;
  fileList: FileItem[];
}

@Options({
    components: {
        UserOutlined,
        MailOutlined,
        MobileOutlined,
        CrownOutlined,
        PlusOutlined,
        LoadingOutlined
    },
    emits: ['close']
})
export default class Password extends Vue {
    // 
    formRef = ref<AntType.Form>()

    // 
    formData: User = {}
    // 
    uploadLoading = false

    // 
    rules: Record<string, Rule[]> = {
        name: [
            { required: true, message: '', validateTrigger: 'blur' },
            { max: 15, message: '15' }
        ],
        post: [{ max: 15, message: '15', validateTrigger: 'blur' }],
        phone: [
            { required: true, message: '', validateTrigger: 'blur' },
            { max: 11, message: '11', validateTrigger: 'blur' },
            { pattern: /^1[0-9]{10}$/, message: '', validateTrigger: 'blur' }
        ],
        email: [
            {
                pattern: /^[_a-z0-9-]+(\.[_a-z0-9-]+)*@[a-z0-9-]+(\.[a-z0-9-]+)*(\.[a-z]{2,})$/,
                message: '',
                validateTrigger: 'blur'
            },
            { max: 25, message: '25', validateTrigger: 'blur' }
        ]
    }

    async mounted() {
        const res = await request.getUserInfo()
        if (res.code === 0) {
            this.formData = res.data
        } else {
            message.error(res.msg)
        }
    }

    // 
    get avatarUrl() {
        // 
        if (!this.formData.newAvatar) {
            return `/file/download/${this.formData.avatar}`
        } else {
            return `/file/download/${this.formData.newAvatar}`
        }
    }

    // 
    get uploadData() {
        return { objectName: this.formData.avatar || '' }
    }

    // 
    beforeUpload(file: FileItem) {
        const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png'
        if (!isJpgOrPng) {
            message.error('JPGPNG')
        }
        const isLt2M = file.size / 1024 / 1024 < 2
        if (!isLt2M) {
            message.error(' 2MB')
        }
        return isJpgOrPng && isLt2M
    }

    // 
    handleUploadChange = (info: FileInfo) => {
        if (info.file.status === 'uploading') {
        this.uploadLoading = true
        } else {
            this.uploadLoading = false
            if (info.file.status === 'done') {
                if (info.file.response?.code === 0) {
                    this.formData.avatar = info.file.response.data.objectName
                    this.formData.newAvatar = this.formData.avatar + '?random=' + Math.random()
                } else {
                    message.error(info.file.response?.msg as string)
                }
            } else if (info.file.status === 'error') {
                message.error('')
            }
        }
    }

    // 
    handleCancel() {
        this.formRef.value?.clearValidate()
        this.$emit('close')
    }

    // 
    handleFinish(values: unknown) {
        const data = toRaw(this.formData)
        request.updateUserInfo(data).then(res => {
            if (res.code === 0) {
                this.handleCancel()
            } else {
                message.error(res.msg)
            }
        })
    }
}
