<template>
    <a-form ref="formRef" name="user-form" :colon="false" :rules="rules" :model="formData" :label-col="{ span: 4 }"
        :wrapper-col="{ span: 20 }" autocomplete="off" @finish="handleFinish">
        <a-form-item has-feedback name="avatar" :wrapper-col="{ offset: 4 }" :style="{marginBottom: '0px'}">
            <a-upload
                name="file"
                :data="uploadData"
                list-type="picture-card"
                class="avatar-uploader"
                :show-upload-list="false"
                action="/file/upload"
                :withCredentials="true"
                @change="handleUploadChange"
                :before-upload="beforeUpload"
                >
                <img v-if="formData.avatar" :src="avatarUrl" alt="" :style="{maxWidth: '100%'}" />
                <div v-else>
                    <plus-outlined v-if="!uploadLoading" />
                    <loading-outlined v-else />
                    <div class="ant-upload-text"></div>
                </div>
                </a-upload>
        </a-form-item>
        <a-form-item has-feedback label="" name="name">
            <a-input v-model:value="formData.name" size="large" placeholder="">
                <template #prefix><UserOutlined style="color: rgba(0, 0, 0, 0.25)" /></template>
            </a-input>
        </a-form-item>
        <a-form-item has-feedback label="" name="phone">
            <a-input v-model:value="formData.phone" size="large" placeholder="">
                <template #prefix><MobileOutlined style="color: rgba(0, 0, 0, 0.25)" /></template>
            </a-input>
        </a-form-item>
        <a-form-item has-feedback label="" name="email">
            <a-input v-model:value="formData.email" size="large" placeholder="">
                <template #prefix><mail-outlined style="color: rgba(0, 0, 0, 0.25)" /></template>
            </a-input>
        </a-form-item>
        <a-form-item has-feedback label="" name="post">
            <a-input v-model:value="formData.post" size="large" placeholder="">
                <template #prefix><crown-outlined style="color: rgba(0, 0, 0, 0.25)" /></template>
            </a-input>
        </a-form-item>
        <a-form-item :wrapper-col="{ span: 20, offset: 4 }" :style="{ textAlign: 'right' }">
            <a-space>
                <a-button @click="handleCancel"></a-button>
                <a-button type="primary" html-type="submit"></a-button>
            </a-space>
        </a-form-item>
    </a-form>
</template>
<script lang="ts">
import { Options, Vue } from 'vue-class-component'
import {
    UserOutlined,
    MailOutlined,
    MobileOutlined,
    CrownOutlined,
    PlusOutlined,
    LoadingOutlined
} from '@ant-design/icons-vue'
import { Rule } from 'ant-design-vue/es/form/interface'
import { ref, toRaw } from 'vue'
import request from '@/request/PlatformRequest'
import { message } from 'ant-design-vue'
import { User } from '@/types/User'
import { R } from '@/types/R'
import PubUtils from '@/utils/PubUtils'

interface FileItem {
  uid: string;
  name?: string;
  status?: string;
  response?: R<any>;
  url?: string;
  type?: string;
  size: number;
  originFileObj: any;
}

interface FileInfo {
  file: FileItem;
  fileList: FileItem[];
}

@Options({
    components: {
        UserOutlined,
        MailOutlined,
        MobileOutlined,
        CrownOutlined,
        PlusOutlined,
        LoadingOutlined
    },
    emits: ['close']
})
export default class Password extends Vue {
    // 
    formRef = ref<AntType.Form>()

    // 
    formData: User = {}
    // 
    uploadLoading = false

    // 
    rules: Record<string, Rule[]> = {
        name: [
            { required: true, message: '', validateTrigger: 'blur' },
            { max: 15, message: '15' }
        ],
        post: [{ max: 15, message: '15', validateTrigger: 'blur' }],
        phone: [
            { required: true, message: '', validateTrigger: 'blur' },
            { max: 11, message: '11', validateTrigger: 'blur' },
            { pattern: /^1[0-9]{10}$/, message: '', validateTrigger: 'blur' }
        ],
        email: [
            {
                pattern: /^[_a-z0-9-]+(\.[_a-z0-9-]+)*@[a-z0-9-]+(\.[a-z0-9-]+)*(\.[a-z]{2,})$/,
                message: '',
                validateTrigger: 'blur'
            },
            { max: 25, message: '25', validateTrigger: 'blur' }
        ]
    }

    async mounted() {
        const res = await request.getUserInfo()
        if (res.code === 0) {
            this.formData = res.data
        } else {
            message.error(res.msg)
        }
    }

    // 
    get avatarUrl() {
        // 
        if (!this.formData.newAvatar) {
            return `/file/download/${this.formData.avatar}`
        } else {
            return `/file/download/${this.formData.newAvatar}`
        }
    }

    // 
    get uploadData() {
        return { objectName: this.formData.avatar || '' }
    }

    // 
    beforeUpload(file: FileItem) {
        const isJpgOrPng = file.type === 'image/jpeg' || file.type === 'image/png'
        if (!isJpgOrPng) {
            message.error('JPGPNG')
        }
        const isLt2M = file.size / 1024 / 1024 < 2
        if (!isLt2M) {
            message.error(' 2MB')
        }
        return isJpgOrPng && isLt2M
    }

    // 
    handleUploadChange = (info: FileInfo) => {
        if (info.file.status === 'uploading') {
        this.uploadLoading = true
        } else {
            this.uploadLoading = false
            if (info.file.status === 'done') {
                if (info.file.response?.code === 0) {
                    this.formData.avatar = info.file.response.data.objectName
                    this.formData.newAvatar = this.formData.avatar + '?random=' + Math.random()
                } else {
                    message.error(info.file.response?.msg as string)
                }
            } else if (info.file.status === 'error') {
                message.error('')
            }
        }
    }

    // 
    handleCancel() {
        this.formRef.value?.clearValidate()
        this.$emit('close')
    }

    // 
    handleFinish(values: unknown) {
        const data = toRaw(this.formData)
        request.updateUserInfo(data).then(res => {
            if (res.code === 0) {
                this.handleCancel()
            } else {
                message.error(res.msg)
            }
        })
    }
}
</script>
<style scoped>
.avatar-uploader > ::v-deep(.ant-upload) {
  width: 150px;
  height: 180px;
}
.ant-upload-select-picture-card i {
  font-size: 32px;
  color: #999;
}

.ant-upload-select-picture-card .ant-upload-text {
  margin-top: 8px;
  color: #666;
}
</style>
